<template>
  <div class="chinese">
    <div class="header">
      <button>
        <router-link to="./">
          <i class="icon iconfont el-icon-yrjiantou"></i>
        </router-link>
      </button>
      <h1>《语文》</h1>
    </div>
    <img src="../assets/Chinese.jpg" style="width:100px" alt="">
    <h3>简介：{{ msg }}</h3>
  </div>
</template>

<script>
export default {
  name: 'chinese',
  data () {
    return {
      msg: '语文是语言文字、语言文学、语言文章或语言文化的简称，其本义是语言文字。语言包括口头语言和书面语言。口头语言较随意，直接易懂，而书面语言讲究准确和语法；文学包括中外古今文学等。而语文中的文章不但有文艺文（文学、曲艺等），还有很多实用文（应用文）。'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header {
  position: relative;
}
.header {
  width: 100%;
  height: 50px;
  margin-top: 0px;
  background-color: antiquewhite;
  position: relative;
}
button {
  height: 60px;
  width: 40px;
  position: absolute;
  left: 10px;
  padding: 0;
  background-color: rgba(255, 255, 255, 0.1);
  border: 0;
}
button i {
  text-decoration: none;
  font-size: 30px;
  line-height: 60px;
  color: black;
}
h1,
h2 {
  margin-top: 0px;
  font-weight: normal;
  color: crimson;
}
ul {
  list-style-type: none;
  padding: 0;
}
</style>



