<template>
  <div class="English">
      <div class="header">
      <button>
        <router-link to="./">
          <i class="icon iconfont el-icon-yrjiantou"></i>
        </router-link>
      </button>
      <h1>《英语》</h1>
    </div>
    <img src="../assets/English.jpg" style="width:100px" alt="">
    <h3>简介：{{ msg }}</h3>
  </div>
</template>

<script>
export default {
  name: 'English',
  data () {
    return {
      msg: '英语（English）是印欧语系-日耳曼语族下的语言，由26个字母组成，英文字母渊源于拉丁字母，拉丁字母渊源于希腊字母，而希腊字母则是由腓尼基字母演变而来的。英语是国际指定的官方语言（作为母语），也是世界上使用最广泛的语言，英语包含约49万词，外加技术名词约30万个，是词汇最多的语言，也是欧盟以及许多国际组织以及英联邦国家的官方语言，拥有世界第三位的母语使用者人数，仅次于汉语和西班牙语母语使用者人数'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header {
  position: relative;
}
.header {
  width: 100%;
  height: 50px;
  margin-top: 0px;
  background-color: antiquewhite;
  position: relative;
}
button {
  height: 60px;
  width: 40px;
  position: absolute;
  left: 10px;
  padding: 0;
  background-color: rgba(255, 255, 255, 0.1);
  border: 0;
}
button i {
  text-decoration: none;
  font-size: 30px;
  line-height: 60px;
  color: black;
}
h1,
h2 {
  margin-top: 0px;
  font-weight: normal;
  color: crimson;
}
ul {
  list-style-type: none;
  padding: 0;
}
</style>



