<template>
  <div class="home">
    <h2>{{ msg }}</h2>
    <div class="big">
      <router-link to="/Chinese">
        <div class="test">
          <i class="icon iconfont el-icon-yryuwen-tianchong"></i>
          <a>语文</a>
        </div>
      </router-link>

      <router-link to="/Math">
        <div class="test">
          <i class="icon iconfont el-icon-yrshuxue-tianchong"></i>
          <a>高数</a>
        </div>
      </router-link>

      <router-link to="/English">
        <div class="test">
          <i class="icon iconfont el-icon-yryingyu-tianchong"></i>
          <a>英语</a>
        </div>
      </router-link>

      <router-link to="/Php">
        <div class="test">
          <i class="icon iconfont el-icon-yrwuli-feichuan"></i>
          <a>PHP</a>
        </div>
      </router-link>

      <router-link to="/Java">
        <div class="test">
          <i class="icon iconfont el-icon-yrjiqiren1"></i>
          <a>JAVA</a>
        </div>
      </router-link>

      <router-link to="/Python">
        <div class="test">
          <i class="icon iconfont el-icon-yrjiqiren"></i>
          <a>Python</a>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      msg: '欢迎登录书籍系统'
    }
  }
}
</script>

<style>
html,
body,
#app {
  height: 100%;
  margin: 0;
  text-decoration: none;
}
.test i{
  width: 50px;
  height: 100px;
}
h2 {
  color: coral;
  font-size: 22px;
}
.home {
  background-color: antiquewhite;
  height: 100%;
  padding-top: 10px;
}
a {
  text-decoration: none;
  color: aliceblue;
}
.big {
    margin: 30px 18px;
}
.test {
  width: 150px;
  height: 150px;
  background-color: cadetblue;
  float: left;
  margin: 17px;
  text-align: center;
  line-height: 150px;
  font-size: 22px;
}
</style>



