import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Chinese from '@/lib/Chinese'
import English from '@/lib/English'
import Math from '@/lib/Math'
import Python from '@/lib/Python'
import Java from '@/lib/Java'
import Php from '@/lib/Php'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/Chinese',
      name: 'chinese',
      component: Chinese
    },
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/English',
      name: 'English',
      component: English
    },
    {
      path: '/Math',
      name: 'Math',
      component: Math
    },
    {
      path: '/Python',
      name: 'Python',
      component: Python
    },
    {
      path: '/Java',
      name: 'Java',
      component: Java
    },
    {
      path: '/Php',
      name: 'Php',
      component: Php
    }
  ]
})



