<template>
  <div class="Python">
      <div class="header">
      <button>
        <router-link to="./">
          <i class="icon iconfont el-icon-yrjiantou"></i>
        </router-link>
      </button>
      <h1>《Python》</h1>
    </div>
    <img src="../assets/Python.jpg" style="width:100px" alt="">
    <h3>简介：{{ msg }}</h3>
  </div>
</template>

<script>
export default {
  name: 'Python',
  data () {
    return {
      msg: 'Python是一种计算机程序设计语言。是一种动态的、面向对象的脚本语言，最初被设计用于编写自动化脚本(shell)，随着版本的不断更新和语言新功能的添加，越来越多被用于独立的、大型项目的开发。'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header {
  position: relative;
}
.header {
  width: 100%;
  height: 50px;
  margin-top: 0px;
  background-color: antiquewhite;
  position: relative;
}
button {
  height: 60px;
  width: 40px;
  position: absolute;
  left: 10px;
  padding: 0;
  background-color: rgba(255, 255, 255, 0.1);
  border: 0;
}
button i {
  text-decoration: none;
  font-size: 30px;
  line-height: 60px;
  color: black;
}
h1,
h2 {
  margin-top: 0px;
  font-weight: normal;
  color: crimson;
}
ul {
  list-style-type: none;
  padding: 0;
}
</style>



