<template>
  <div class="Math">
    <div class="header">
      <button>
        <router-link to="./">
          <i class="icon iconfont el-icon-yrjiantou"></i>
        </router-link>
      </button>
      <h1>《高数》</h1>
    </div>
    <img src="../assets/Math.jpg" style="width:100px" alt="">
    <h3>简介：{{ msg }}</h3>
  </div>
</template>

<script>
export default {
  name: 'Math',
  data () {
    return {
      msg: '广义地说，初等数学之外的数学都是高等数学，也有将中学较深入的代数、几何以及简单的集合论初步、逻辑初步称为中等数学的，将其作为中小学阶段的初等数学与大学阶段的高等数学的过渡。'
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header {
  position: relative;
}
.header {
  width: 100%;
  height: 50px;
  margin-top: 0px;
  background-color: antiquewhite;
  position: relative;
}
button {
  height: 60px;
  width: 40px;
  position: absolute;
  left: 10px;
  padding: 0;
  background-color: rgba(255, 255, 255, 0.1);
  border: 0;
}
button i {
  text-decoration: none;
  font-size: 30px;
  line-height: 60px;
  color: black;
}
h1,
h2 {
  margin-top: 0px;
  font-weight: normal;
  color: crimson;
}
ul {
  list-style-type: none;
  padding: 0;
}
</style>



