<template>
  <div class="Php">
    <div class="header">
      <button>
        <router-link to="./">
          <i class="icon iconfont el-icon-yrjiantou"></i>
        </router-link>
      </button>
      <h1>《PHP》</h1>
    </div>
    <img src="../assets/PHP.jpg" style="width:100px" alt="">
    <h3>简介：{{ msg }}</h3>
  </div>
</template>

<script>
export default {
  name: 'Php',
  data () {
    return {
      msg: 'PHP（外文名:PHP: Hypertext Preprocessor，中文名：“超文本预处理器”）是一种通用开源脚本语言。语法吸收了C语言、Java和Perl的特点，利于学习，使用广泛，主要适用于Web开发领域。PHP 独特的语法混合了C、Java、Perl以及PHP自创的语法。它可以比CGI或者Perl更快速地执行动态网页。用PHP做出的动态页面与其他的编程语言相比，PHP是将程序嵌入到HTML（标准通用标记语言下的一个应用）文档中去执行，执行效率比完全生成HTML标记的CGI要高许多；PHP还可以执行编译后代码，编译可以达到加密和优化代码运行，使代码运行更快。'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header {
  position: relative;
}
.header {
  width: 100%;
  height: 50px;
  margin-top: 0px;
  background-color: antiquewhite;
  position: relative;
}
button {
  height: 60px;
  width: 40px;
  position: absolute;
  left: 10px;
  padding: 0;
  background-color: rgba(255, 255, 255, 0.1);
  border: 0;
}
button i {
  text-decoration: none;
  font-size: 30px;
  line-height: 60px;
  color: black;
}
h1,
h2 {
  margin-top: 0px;
  font-weight: normal;
  color: crimson;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



